import Vue from 'vue';
import Vuex from "vuex"

Vue.use(Vuex)

export const store = new Vuex.Store({

    state:{
        token:"",
        datos:{},
        foto:"",
        lugar:""
    },

    mutations:{
        setToken(state,valor){
            state.token=valor
        },
        setDatos(state,valor){
            state.datos=valor
        },
        setFoto(state,value){
            state.foto=value
        },
        setLugar(state, value){
            state.lugar=value
        }
    },

    actions:{
        setToken(context,data){
            context.commit("setToken", data)
        },
        setDatos(context,data){
            context.commit("setDatos", data)
        },
        setFoto(context,data){
            context.commit("setFoto", data)
        },
        setLugar(context, data){
            context.commit("setLugar", data)
        }
    }

})