<template>
  <div>
    <v-container fluid class="mt-16">
      <v-row>
        <v-col cols="12" md="4"> </v-col>
        <v-col cols="12" md="4">
          <v-card>
            <v-card-title class="black white--text">
              <span>Cambio de contraseña</span>
            </v-card-title>
            <v-card-text>
              <v-col cols="12">
                <v-text-field
                  :append-icon="show3 ? 'mdi-eye' : 'mdi-eye-off'"
                  :type="show3 ? 'text' : 'password'"
                  name="input-10-2"
                  label="Nueva contraseña"
                  v-model="nueva1"
                  class="input-group--focused"
                  @click:append="show3 = !show3"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  name="input-10-2"
                  label="Confirmar la nueva contraseña"
                  v-model="nueva2"
                  class="input-group--focused"
                ></v-text-field>
              </v-col>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn @click="guardar()" dense class="orange" elevation="2"
                >Guardar</v-btn
              >
            </v-card-actions>
          </v-card>
        </v-col>
        <v-col cols="12" md="4"> </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import axios from "axios";
import Swal from "sweetalert2";
export default {
  name: "ResetearPassword",
  data() {
    return {
      show3: false,
      nueva1: "",
      nueva2: "",
    };
  },
  methods: {
    guardar() {
      const valores = window.location.search;
      console.log(valores);
      let urlParametros = new URLSearchParams(valores);
      let param1 = urlParametros.get("token");
      let param2 = urlParametros.get("id");
      let header = { headers: { "x-token": param1 } };
      if (this.nueva1 === this.nueva2) {
        axios
          .post(
            `/api/holder/passwordreset`,
            {
              holderId: param2,
              password: this.nueva2,
            },
            header
          )
          .then((resp) => {
            console.log(resp);
            Swal.fire({
              position: "bottom-end",
              icon: "success",
              title: "Cambio de contraseña exitoso",
              showConfirmButton: false,
              timer: 2000,
            });
          })
          .catch((err) => {
            console.log(err);
            Swal.fire({
              title: "<strong>Error en registro</strong>",
              icon: "error",
              html: `<h4>${err.response.data.errors[0].msg}</h4>`,
              showCloseButton: true,
            });
          });
      } else {
        Swal.fire({
          title: "<strong>Error en contraseña</strong>",
          icon: "error",
          html: `<h4>No coinciden las contraseñas</h4>`,
          showCloseButton: true,
        });
        this.nueva1 = "";
        this.nueva2 = "";
      }
    },
  },
};
</script>