<template>
  <v-app class="mt-16">
    <template>
      <v-container fluid class="mt-5">
        <v-row>
          <v-col cols="6">
            <v-card class="elevation-0">
              <h2>Holders</h2>
            </v-card>
          </v-col>
          <v-col cols="6">
            <v-card class="elevation-0 d-flex justify-end">
              <v-btn @click="nuevoHolder()">Nuevo Usuario</v-btn>
            </v-card>
          </v-col>
        </v-row>
      </v-container>

      <!-- tabla de datos de los holders -->

      <v-data-table
        :headers="encabezados"
        :items="holders"
        :items-per-page="20"
        class="elevation-1 mt-5 mx-5"
      >
        <template v-slot:[`item.opciones`]="{ item }">
          <template v-if="item.state === '1'">
            <v-icon class="mr-2" color="red" @click="editar(item)">
              mdi-account-off</v-icon
            >
          </template>
          <template v-else>
            <v-icon class="mr-2" color="green" @click="editar(item)"
              >mdi-account-check</v-icon
            >
          </template>
          <template>
            <v-icon class="mr-2" color="orange" @click="abrir(item)">
              mdi-pencil</v-icon
            >
          </template>
        </template>
        <template v-slot:[`item.state`]="{ item }">
          <span class="green--text" v-if="item.state === '1'">Activo</span>
          <span class="red--text" v-else>Inactivo</span>
        </template>
      </v-data-table>
    </template>

    <!-- Cuadro de dialogo para insertar holder -->

    <v-dialog v-model="dialog" persistent max-width="600px">
      <v-toolbar
          dark
          color="orange"
        >
          <v-btn
            icon
            dark
            @click="cerrarDialog()"
          >
            <v-icon>mdi-close</v-icon>
          </v-btn>
          <v-toolbar-title>{{titulo}}</v-toolbar-title>
          </v-toolbar>
      <v-card>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="12" sm="6" md="6">
                <v-text-field
                  label="Documento"
                  type="number"
                  v-model="documento"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="6">
                <v-text-field
                  label="Ficha"
                  type="number"
                  v-model="ficha"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  label="Nombre Completo"
                  v-model="nombre"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  label="Telefono"
                  v-model="telefono"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field label="Correo" v-model="correo"></v-text-field>
              </v-col>
              <v-col cols="12" v-if="bd!=true">
                <v-text-field
                  label="Password"
                  v-model="pass"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6">
                <v-select
                  :items="[
                    'ADMIN',
                    'PUBLICO',
                    'APRENDIZ',
                    'INSTRUCTOR',
                    'ADMINISTRATIVO',
                    'BIBLIOTECA',
                    'BIBLIOTECARIA',
                    'PORTERIA',
                    'GUARDA',
                  ]"
                  label="Perfil"
                  v-model="perfil"
                ></v-select>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="cerrarDialog()">
            Cerrar
          </v-btn>
          <v-btn color="blue darken-1" text @click="accionHolder()">
            Guardar
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-overlay :value="overlay">
      <v-progress-circular indeterminate size="64"></v-progress-circular>
    </v-overlay>
  </v-app>
</template>

<script>
import axios from "axios";
import Swal from "sweetalert2";
export default {
  name: "PagePersona",
  data() {
    return {
      dialog: false,
      overlay: false,
      titulo:"Guardar Holder",
      id:"",
      bd:false,
      nombre: "",
      pass: "",
      documento: "",
      telefono: "",
      correo: "",
      perfil: "",
      ficha: "",
      encabezados: [
        {
          text: "Documento",
          align: "start",
          sortable: false,
          value: "document",
        },
        { text: "Nombre", value: "name" },
        { text: "Telefono", value: "phone" },
        { text: "Correo", value: "email" },
        { text: "Ficha", value: "ficha", sortable: false },
        { text: "Rol", value: "rol", sortable: false },
        { text: "Estado", value: "state", sortable: false },
        { text: "Opciones", value: "opciones", sortable: false },
      ],
      holders: [],
    };
  },
  methods: {

    //funcion para traer todos los holders

    listarHolders() {
      let header = { headers: { "x-token": this.$store.state.token } };
      axios
        .get("/api/holder", header)
        .then((response) => {
          console.log(response);
          this.holders = response.data.holder;

        })
        .catch((error) => {
          console.log(error);
        });
    },

    //metodo para el boton de nuevo holder abre el cuadro de dialogo

    nuevoHolder() {
      this.dialog = true;
      this.bd=false
      this.titulo="Guardar Holder"
    },

    //metodo que maneja el boton de guardar del dialogo
    // y decide si guarda o edita

    accionHolder(){
      if(this.bd===true){
        console.log("estoy editando..");
        this.editarHolder()
        
      }else{
        console.log("estoy agregando..");
        this.agregarHolder()
        
      }
    },

    //metodo que limpia las cajas del formulario del dialogo 

    limpiarCajas(){
      this.id=""
      this.nombre=""
      this.documento="",
      this.telefono=""  
      this.correo="" 
      this.pass=""
      this.perfil=""  
      this.ficha=""  
    },

    //metodo que esta asignado al boton editar de la tabla
    //abre el dialogo, edita el titulo y lleva los datos al formulario 

    abrir(item) {
      console.log(item);
      this.titulo="Editar Holder"
      this.bd=true
      this.dialog = true;
      this.id=item._id
      console.log(this.id);
      this.nombre=item.name,
      this.documento=item.document,
      this.telefono=item.phone,
      this.correo=item.email,
      this.perfil=item.rol,
      this.ficha=item.ficha
    },

    //metodo que edita holder en la base de datos

    editarHolder(){
      this.overlay = true;
      let header = { headers: { "x-token": this.$store.state.token } };
      console.log(this.documento);
      axios
        .put(
          `/api/holder/${this.id}`,
          {
            document: this.documento,
            name: this.nombre,
            ficha: this.ficha,
            phone: this.telefono,
            rol: this.perfil,
          },
          header
        )
        .then((response) => {
          console.log(response);
          Swal.fire({
            position: "bottom-end",
            icon: "success",
            title: "Actualización exitosa",
            showConfirmButton: false,
            timer: 2000,
          });
          this.listarHolders()
          this.limpiarCajas();
          this.dialog = false;
          this.overlay = false;
        })
        .catch((error) => {
          console.log(error);
          Swal.fire({
            title: "<strong>Actualización rechazada</strong>",
            icon: "error",
            html: `<h4>${error.response.data.errors[0].msg}</h4>`,
            showCloseButton: true,
          });
          this.overlay = false;
        });
    },

    //metodo que guarda holder en la base de datos

    agregarHolder() {
      this.overlay = true;
      axios
        .post("/api/holder", {
          document: this.documento,
          name: this.nombre,
          email: this.correo,
          password: this.pass,
          phone: this.telefono,
          ficha: this.ficha,
          rol: this.perfil,
        })
        .then((response) => {
          console.log(response);
          Swal.fire({
            position: "bottom-end",
            icon: "success",
            title: "Registro exitoso",
            showConfirmButton: false,
            timer: 2000,
          });
          this.overlay = false;
          this.listarHolders()
          this.limpiarCajas();
          
        })
        .catch((error) => {
          console.log(error);
          Swal.fire({
            title: "<strong>Error en registro</strong>",
            icon: "error",
            html: `<h4>${error.response.data.errors[0].msg}</h4>`,
            showCloseButton: true,
          });
          this.overlay = false;
        });
    },

    //cierra el dialogo y blanquea cajas

    cerrarDialog() {
      this.dialog = false;
      this.limpiarCajas()
    },

    //metodo que activa y desactiva los holder esta asignado al boton
    // activa y desactivar de la tabla

    editar(item) {
      console.log(item.state);
      if (item.state === "1") {
        let header = { headers: { "x-token": this.$store.state.token } };
        axios
          .put(
            `/api/holder/unactivate/${item._id}`,
            {},
            header
          )
          .then((response) => {
            console.log(response);
            this.listarHolders();
          })
          .catch((error) => {
            console.log(error.response);
          });
      } else {
        let header = { headers: { "x-token": this.$store.state.token } };
        axios
          .put(
            `/api/holder/activate/${item._id}`,
            {},
            header
          )
          .then((response) => {
            console.log(response);
            this.listarHolders();
          })
          .catch((error) => {
            console.log(error.response);
          });
      }
    },
  },
  mounted() {
    this.listarHolders();
  },
};
</script>