<template>
    <v-app class="mt-16">
      <!-- Codigo que contiene la tabla de datos de todos los laptop -->
      <template v-if="$store.state.token !== ''">
        <v-container fluid class="mt-5">
          <v-row>
            <v-col cols="6">
              <v-card class="elevation-0">
                <h2>Registro de entradas y salidas</h2>
              </v-card>
            </v-col>
            <v-col cols="6">
              <v-card v-if="$store.state.datos.rol === 'ADMIN' ||
              $store.state.datos.rol === 'BIBLIOTECARIA' ||
              $store.state.datos.rol === 'GUARDA'" 
              class="elevation-0 d-flex justify-end">
                <v-btn @click="nuevoEquipo()">Nuevo Registro</v-btn>
              </v-card>
            </v-col>
          </v-row>
        </v-container>
        <v-data-table
          :headers="encabezados"
          :items="entradas"
          :items-per-page="20"
          class="elevation-1 mt-5 mx-5"
        >
            <template slot="no-data">
                <div><span>No hay datos por mostrar</span></div>
            </template>
          <template v-slot:[`item.opciones`]="{ item }">
            <v-tooltip top>
            <template v-slot:activator="{ on, attrs }" v-if="fechaSalida(item.checkout)==='No se ha devuelto el pc' && $store.state.datos.rol==='ADMIN' || $store.state.datos.rol==='BIBLIOTECA' || $store.state.datos.rol==='BIBLIOTECARIA' || $store.state.datos.rol==='PORTERIA' || $store.state.datos.rol==='GUARDA'">
              <v-icon  v-bind="attrs" v-on="on" class="mr-2" color="orange" @click="devolver(item)">
                 mdi-keyboard-return</v-icon>
            </template>
            <span>Retorno del pc</span>
            </v-tooltip>
          </template>
          <template v-slot:[`item.entrytime`]="{ item }">
            <span>{{ fechaEntrada(item.entrytime) }}</span>
          </template>
          <template v-slot:[`item.checkout`]="{ item }">
            <span class="red--text" v-if="fechaSalida(item.checkout)==='No se ha devuelto el pc'">{{ fechaSalida(item.checkout) }}</span>
            <span class="green--text" v-else>{{ fechaSalida(item.checkout)}}</span>
          </template>
        </v-data-table>
      </template>
  
      <!-- Codigo para mostrar en caso que no haya una sesion activa -->
      <v-container v-else>
        <v-row>
          <v-col> </v-col>
          <v-col class="d-flex justify-center">
            <v-img
              src="../assets/images/good-night-sweet-dreams.gif"
              height="250"
              width="250"
            >
            </v-img>
          </v-col>
          <v-col> </v-col>
        </v-row>
        <v-row>
          <v-col>
            <v-alert outlined text type="error">
              <v-row align="center">
                <v-col class="grow">
                  En el momento su sesion ha caducado ❗ Por favor inicie sesion
                  para interactuar con los servicios de biblioteca
                </v-col>
                <v-col class="shrink">
                  <v-btn @click="login()" outlined color="red">
                    <v-icon left> mdi-login </v-icon> Login
                  </v-btn>
                </v-col>
              </v-row>
            </v-alert>
          </v-col>
        </v-row>
      </v-container>
      <!-- Cuadro de dialogo para insertar laptop -->
  
      <v-dialog v-model="dialog" persistent max-width="600px">
        <v-card>
          <v-card-title>
            <span class="text-h5">Registrar nueva entrada</span>
          </v-card-title>
          <v-card-text>
            <v-container>
              <v-row>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field
                    label="Quien registra"
                    type="text"
                    disabled
                    v-model="$store.state.datos.name"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field
                    label="Serial del pc"
                    type="text"
                    v-model="serial"
                  ></v-text-field>
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field
                    label="Documento del aprendiz"
                    type="text"
                    v-model="documento"
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-container>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" text @click="cerrarDialog()">
              Cerrar
            </v-btn>
            <v-btn color="blue darken-1" text @click="registrar_entrada()">
              Guardar
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
      <v-overlay :value="overlay">
        <v-progress-circular indeterminate size="64"></v-progress-circular>
      </v-overlay>
    </v-app>
  </template>
  
  <script>
  import axios from "axios";
  import Swal from "sweetalert2";
  export default {
    name: "PageRegistro",
    data() {
      return {
        overlay:false,
        fecha: "",
        dialog: false,
        serial: "",
        documento:"",
        lugar:"",
        entradas: [],
        span:false,
        id_propietario: "",
        encabezados: [
          { text: "Rol", value: "holder.rol" },
          {
            text: "Persona encargada",
            align: "start",
            sortable: false,
            value: "holder.name",
          },
          { text: "Serial PC", value: "laptop.serial" },
          { text: "Propietario", value: "laptop.holder.name" },
          { text: "Registro Inicial", value: "entrytime", sortable: false },
          {
            text: "Aprendiz",
            value: "documentlearner.name",
            sortable: false,
          },
          { text: "Registro final", value: "checkout", sortable: false },
          { text: "Opciones", value: "opciones", sortable: false },
        ],
      };
    },
    methods: {
      listarPrestamos() {
        
        let header = { headers: { "x-token": this.$store.state.token } };
        axios.get(`/api/entry/date/pendientes/entrega/holder/${this.$store.state.datos._id}`,header)
        .then(res =>{
          console.log(res);
          this.entradas = res.data.entry;
        })
        .catch(err =>{
          console.log(err);
        })
      },
  
      registrar_entrada() {
       console.log(this.$store.state.lugar);
        let header = { headers: { "x-token": this.$store.state.token } };
        axios
          .post(
            "/api/entry",
            {
              holder: this.$store.state.datos._id,
              laptop: this.serial,
              documentlearner:this.documento,
              checkout: "",
              place:this.$store.state.lugar
            },
            header
          )
          .then((response) => {
            console.log(response);
            Swal.fire({
              position: "bottom-end",
              icon: "success",
              title: "Registro exitoso",
              showConfirmButton: false,
              timer: 2000,
            });
            this.listarPrestamos();
          })
          .catch((error) => {
            console.log(error);
            Swal.fire({
              title: "<strong>Error en registro</strong>",
              icon: "error",
              html: `<h4>${error.response.data.errors[0].msg}</h4>`,
              showCloseButton: true,
            });
          });
      },
  
      devolver(item) {
        console.log(item);
        let header = { headers: { "x-token": this.$store.state.token } };
        axios
          .put(
            `/api/entry/${item._id}`,
            {},
            header
          )
          .then((res) => {
            console.log(res);
            this.listarPrestamos();
          })
          .catch((error) => {
            console.log(error);
          });
      },
  
  
      login() {
        this.$router.replace("/");
      },
  
      //metodo para abrir cuadro de dialogo para insertar uno
      nuevoEquipo() {
        this.dialog = true;
      },
      //metodo para abrir cuadro de dialogo para eliminar uno
      cerrarDialog() {
        this.dialog = false;
      },
      fechaEntrada(r) {
        let d = new Date(r);
        return d.toLocaleDateString() +" - "+ d.toLocaleTimeString()
      },
      fechaSalida(r) {
        if (r === undefined || r === "") {
          this.span=false
          return "No se ha devuelto el pc";
        } else {
          let d = new Date(r);
          this.span=true
          return d.toLocaleDateString() +" - "+ d.toLocaleTimeString();
        }
      },
    },
  
    computed: {},
  
    created() {
      this.listarPrestamos();
    },
  };
  </script>