<template>
  <v-app class="mt-16">
    <!-- Codigo que contiene la tabla de datos de todos los laptop -->
    <template v-if="$store.state.token !== ''">
      <v-container fluid class="mt-5">
        <v-row>
          <v-col cols="6">
            <v-card class="elevation-0">
              <h2>Computadoras</h2>
            </v-card>
          </v-col>
          <v-col cols="6">
            <v-card class="elevation-0 d-flex justify-end">
              <v-btn @click="nuevoEquipo()">Nuevo Equipo</v-btn>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
      <v-data-table
        :headers="encabezados"
        :items="laptops"
        :items-per-page="20"
        class="elevation-1 mt-5 mx-5"
      >
        <template v-slot:[`item.opciones`]="{ item }">
          <template v-if="item.state === '1'">
            <v-icon class="mr-2" color="red" @click="habilitar(item)">
              mdi-account-off</v-icon
            >
          </template>
          <template v-else>
            <v-icon class="mr-2" color="green" @click="habilitar(item)"
              >mdi-account-check</v-icon
            >
          </template>
          <v-tooltip left>
            <template v-slot:activator="{ on, attrs }">
              <v-icon
                dark
                v-bind="attrs"
                v-on="on"
                class="mr-2"
                color="green"
                @click="generarcodigo(item)"
                >mdi-barcode</v-icon
              >
            </template>
            <span>Generar Codigo</span>
          </v-tooltip>
        </template>
        <template v-slot:[`item.state`]="{ item }">
          <span class="green--text" v-if="item.state === '1'">Activo</span>
          <span class="red--text" v-else>Inactivo</span>
        </template>
        <template v-slot:[`item.createdAt`]="{ item }">
          <span
            >{{ item.createdAt.substring(0, 10) }}
            {{ item.createdAt.substring(11, 19) }}</span
          >
        </template>
      </v-data-table>
    </template>

    <!-- Codigo para mostrar en caso que no haya una sesion activa -->
    <v-container v-else>
      <v-row>
        <v-col> </v-col>
        <v-col class="d-flex justify-center">
          <v-img
            src="../assets/images/good-night-sweet-dreams.gif"
            height="250"
            width="250"
          >
          </v-img>
        </v-col>
        <v-col> </v-col>
      </v-row>
      <v-row>
        <v-col>
          <v-alert outlined text type="error">
            <v-row align="center">
              <v-col class="grow">
                En el momento su sesion ha caducado ❗ Por favor inicie sesion
                para interactuar con los servicios de biblioteca
              </v-col>
              <v-col class="shrink">
                <v-btn @click="login()" outlined color="red">
                  <v-icon left> mdi-login </v-icon> Login
                </v-btn>
              </v-col>
            </v-row>
          </v-alert>
        </v-col>
      </v-row>
    </v-container>
    <!-- Cuadro de dialogo para insertar laptop -->

    <v-dialog v-model="dialog" persistent max-width="600px">
      <v-card>
        <v-card-title>
          <span class="text-h5">Guardar Perfil</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="12" sm="6" md="6">
                <v-text-field
                  label="Persona Encargada"
                  type="text"
                  disabled
                  v-model="$store.state.datos.name"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="6">
                <v-text-field
                  label="Serial"
                  type="text"
                  v-model="serial"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  label="Estado"
                  v-model="estado"
                  disabled
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-textarea
                  label="Observaciones"
                  v-model="observaciones"
                ></v-textarea>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="cerrarDialog()">
            Cerrar
          </v-btn>
          <v-btn color="blue darken-1" text @click="registrar_laptop()">
            Guardar
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Cuadro de dialogo para generar el codigo -->

    <v-dialog v-model="dialog2" max-width="600">
      <div class="d-flex justify-center white">
        <v-card elevation="0">
          <v-card-title class="d-flex justify-center white">
            <span color="green" class="d-flex justify-center"
              >Codigo Generado ✅</span
            >
          </v-card-title>
          <div id="s"></div>
          <br />
        </v-card>
      </div>
      
    </v-dialog>
    <v-overlay :value="overlay" absolute="true">
      <v-progress-circular indeterminate size="64"></v-progress-circular>
    </v-overlay>
  </v-app>
</template>

<script>
import axios from "axios";
import Swal from "sweetalert2";
export default {
  name: "PageEquipo",
  data() {
    return {
      overlay:false,
      dialog: false,
      dialog2: false,
      id: "",
      persona: "",
      estado: "Activo",
      serial: "",
      observaciones: "",
      encabezados: [
        {
          text: "Persona encargada",
          align: "start",
          sortable: false,
          value: "holder.name",
        },
        { text: "Serial", value: "serial" },
        { text: "Estado", value: "state" },
        { text: "Creado", value: "createdAt", sortable: false },
        { text: "Observaciones", value: "observations", sortable: false },
        { text: "Opciones", value: "opciones", sortable: false },
      ],
      laptops: [],
    };
  },
  methods: {
    //metodo para el boton de mensaje sesion caducada
    login() {
      this.$router.replace("/");
    },

    //metodo para traer todos los laptops de la bd
    listarLaptops() {
      let header = { headers: { "x-token": this.$store.state.token } };
      console.log(this.$route.params.id);
      this.id = this.$route.params.id;
      /*  let url = this.$route.params.id === "" || this.$route.params.id === undefined ? "/api/laptop":`/api/laptop/holder/${this.id}` */
      let url = "/api/laptop";
      axios
        .get(url, header)
        .then((response) => {
          console.log(response);
          this.laptops = response.data.laptop;
        })
        .catch((error) => {
          console.log(error);
        });
      this.id = "";
    },

    //metodo para abrir cuadro de dialogo para insertar uno
    nuevoEquipo() {
      this.dialog = true;
    },

    //metodo para abrir cuadro de dialogo para eliminar uno
    cerrarDialog() {
      this.dialog = false;
    },

    //metodo para crear codigo
    generarcodigo(item) {
      let header = { headers: { "x-token": this.$store.state.token } };
      console.log(header);
      this.dialog2 = true;
      axios
        .put(
          `/api/laptop/barcodehtml/${item._id}`,
          {},
          header
        )
        .then((res) => {
          console.log(res);
          this.vector = res.data;
          let ve = document.getElementById("s");
          ve.innerHTML = this.vector;
        })
        .catch((err) => {
          console.log(err);
        });
    },

    //metodo para registrar una nueva laptop
    registrar_laptop() {
      this.dialog=false
      this.overlay = true;
      let header = { headers: { "x-token": this.$store.state.token } };
      axios
        .post(
          "/api/laptop",
          {
            holder: this.$store.state.datos._id,
            serial: this.serial,
            observations: this.observaciones,
          },
          header
        )
        .then((response) => {
          console.log(response);
          Swal.fire({
            position: "bottom-end",
            icon: "success",
            title: "Registro exitoso",
            showConfirmButton: false,
            timer: 2000,
          });
          this.listarLaptops();
          this.limpiarCajas()
          
          this.overlay = false;
        })
        .catch((error) => {
          console.log(error);
          Swal.fire({
            title: "<strong>Error en registro</strong>",
            icon: "error",
            html: `<h4>${error.response.data.errors[0].msg}</h4>`,
            showCloseButton: true,
          });
          this.overlay = false;
        });
    },

    limpiarCajas(){
      this.serial=""
      this.estado=""
      this.observaciones=""
    },

    //metodo para activar y desactivar una laptop
    habilitar(item) {
      console.log(item.state);
      if (item.state === "1") {
        let header = { headers: { "x-token": this.$store.state.token } };
        axios
          .put(
            `/api/laptop/unactivate/${item._id}`,
            {},
            header
          )
          .then((response) => {
            console.log(response);
            this.listarLaptops();
          })
          .catch((error) => {
            console.log(error.response);
          });
      } else {
        let header = { headers: { "x-token": this.$store.state.token } };
        axios
          .put(
            `/api/laptop/activate/${item._id}`,
            {},
            header
          )
          .then((response) => {
            console.log(response);
            this.listarLaptops();
          })
          .catch((error) => {
            console.log(error.response);
          });
      }
    },
  },
  mounted() {
    this.listarLaptops();
  },
};
</script>

